$column: 12;
$gutter: 30px;

//container  bootstrap 
// 

.container {
  max-width: pxToRem($container-width);
  margin: {
    top: 0;
    right: auto;
    bottom: 0;
    left: auto;
  }
  @include pad {
    max-width: pxToRem(960px);
    padding: {
      left: pxToRem(39px);
      right: pxToRem(39px);
    }
  }
  @include mobile {
    max-width: pxToRem(720px);
    padding: {
      left: pxToRem(15px);
      right: pxToRem(15px);
    }
  }
}


.row {
  display: flex;
  flex-wrap: wrap;
  margin: {
    left: -$gutter/2;
    right: -$gutter/2;
  }
  > *{
    flex-shrink: 0;
    width: 100%;
    max-width: 100%;
    padding: {
      left: $gutter/2;
      right: $gutter/2;
    }
  }
  &.no-gutters{
    > *{
      padding: {
        left: 0;
        right: 0;
      }
    }
  }
}

// grid col
@for $i from 1 through $column{
  .col-#{$i}{
    flex: 0 0 auto;
    width: (100%/$column) * $i;
  }
}

// grid col-lg
@for $i from 1 through $column{
  @include pad{
    .col-lg-#{$i}{
      flex: 0 0 auto;
      width: (100%/$column) * $i;
    }
  }
